} */
.recipe-card {
  align-items: center;
  background: linear-gradient(to top left, #ff7b00, #fda65d);
  border-radius: 15px;
  box-shadow: 8px 15px 20px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column-reverse;
  margin-top: 160px;
  max-height: 150px;
  max-width: 280px;
  min-height: 125px;
  transition: all ease 0.6s;
}

.recipe-card:hover {
  background: linear-gradient(to top left, #fda65d, #ff7b00, #ff7b00);
  box-shadow: 8px 15px 20px rgba(0, 0, 0, 0.4);
  transform: translateY(-20px);
}

.recipe-image {
  border-radius: 250px;
  height: 260px;
  width: 260px;
}

.recipe-title {
  color: black;
  font-family: Sanchez , serif;
  font-size: 20px;
  font-weight: 700;
  text-align: center;
}

a:hover {
  text-decoration: none;
}

.details-start-recipe {
  bottom: 0;
  position: fixed;
}

.details-image {
  border-radius: 20px;
  margin: 20px;
  width: 350px;
}

button {
  background-color: #ff7b00;
  border: none;
  border-radius: 18px;
  margin: 5px 2.5px;
  outline: none;
  padding: 10px;
  transition: all ease 0.5s;
}

button:hover {
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.8);
  outline: none;
}

.header-container {
  align-items: center;
  display: flex;
  justify-content: space-between;
  margin-bottom: 25px;
  width: 100%;
}

.header-container-expanded {
  display: flex;
  flex-direction: column;
  text-align: center;
  transition: all ease 0.5s;
  width: 100%;
}

.header-explore {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-bottom: 25px;
  width: 100%;
}

.page-title {
  font-family: Sanchez , serif;
  font-size: 20px;
  font-weight: 800;
  margin-top: 10px;
}

.search-input {
  border: 1px solid grey;
  border-radius: 18px;
  height: 50px;
  margin-top: 7px;
  width: 97%;
}

.search-input:hover {
  border: 2px solid #e26a2c;
  border-radius: 18px;
  outline: none;
}

.filter-options {
  display: flex;
  font-family: Sanchez , serif;
  justify-content: space-around;
  margin: 10px 0;
  width: 100%;
}

.filter-options label {
  align-items: center;
  display: flex;
  justify-content: space-between;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.filter-options input {
  margin-right: 5px;
}

.search-button {
  background-color: #e26a2c;
  border-radius: 8px;
  font-size: 18px;
  font-weight: 700;
  width: 100%;
}

.footer-menu {
  align-items: center;
  background-color: #e26a2c;
  bottom: 0;
  display: flex;
  justify-content: space-around;
  padding: 2px;
  position: fixed;
  width: 100%;
}

 body {
   background-color: #eee9e5;
