 .explore-wrapper {
  align-items: center;
  background-color: #eee9e5;
  background-size: 100vh;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
 }

.explore-btn-container {
  display: flex;
  flex-wrap: wrap;
  font-family: Sanchez , serif;
  justify-content: center;
  margin-bottom: 20px;
}
.loading-gif {
  display: flex;
  margin: auto;
  width: 365px;
}

.loading-container {
  align-items: center;
  background-color: #d65210;
  display: flex;
  height: 100vh;
}

.loading-gif {
  border-radius: 100vh;
  display: flex;
  width: 55vh;
}

.food-recipes-container {
  align-items: center;
  background-color: #eee9e5;
  background-size: 100vh;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.categories-container {
  display: flex;
  flex-wrap: wrap;
  font-family: Sanchez , serif;
  justify-content: center;
  margin-bottom: 20px;
}

.categories-container button {
  background-color: #fda65d;
  margin-left: 10px;
}

.categories-container button:hover {
  background-color: #ff7b00;
}


.recipes-container {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  grid-row-gap: 40px;
  row-gap: 40px;
}

.login-page {
  background-image: url(/recipes-app/static/media/raining.9789779f.gif);
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
}

.login-container {
  background-color: #f78812;
  border-radius: 15px;
  box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.7);
  display: flex;
  flex-direction: column;
  height: 38vh;
  justify-content: center;
  margin: 0 15px;
  margin-top: -20px;
  padding: 18px;
}

.login-container input {
  border: 1px solid gray;
  border-radius: 10px;
  font-size: 18px;
  font-weight: 800;
  height: 8vh;
  margin: 7px 0;
  outline: none;
  text-align: center;
  transition: all ease 0.1s;
}

.login-container input:hover {
  border: 2px solid #468233;
}

.login-container button {
  background-color: #d65210;
  box-shadow: 2px 5px 10px rgba(0, 0, 0, 0.9);
  font-family: Sanchez , serif;
  font-weight: 700;
  height: 8vh;
}


.strikethrough {
  text-decoration: line-through;
}

.not-found-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.not-found-title {
  font-size: calc(100% + 4.8vmin);
  margin: auto;
  text-align: center;
}

.not-found-subtitle {
  font-size: calc(60% + 0.8vmin);
  margin-top: -40px;
  text-align: center;
}

.not-found-gif {
  margin: auto;
  width: 300px;
}

